import axios from 'axios'
import router from '../vue-router'

// 默认 30s的超时时间
const TIME_OUT = 30000 

// create an axios instance
const service = axios.create({
  baseURL: R_URL.BASE_URL, // api的base_url
  timeout: TIME_OUT, // request timeout
})

// 请求拦截
service.interceptors.request.use((config) => {
  // 每次请求，请求头中加上`token`
  const token = localStorage.getItem('login_token');
  token && (config.headers.token = token)
  return config;
}, error => Promise.reject(error))

// 响应拦截
service.interceptors.response.use(
  (response) => {
    const { token } = response.headers
    token && localStorage.setItem('login_token', token)
    return response
  },
  (error) => {
    // 如果 token 验证失败则跳回登陆页，并执行 axios then 方法错误的回调
    if (error.response.status === 401) {
      router.push('/login');
      return Promise.reject(error.response.statusText);
    }
    // 如果报错的时候，服务器返回了错误消息，则把错误消息和错误码都放到errorMessage和errorCode上面
    // 方便获取
    if (error.response.status === 500) {
      localStorage.removeItem('login_token')
      router.push('/login')
    }

    return Promise.reject(error)
  }
)

export default service
