<template>
  <div id="cl">
    <div class="c-main">

      <div class="m-header">
        <!-- 查询栏 -->
        <a-row :gutter="16">
          <a-col :span="5">
            <a-date-picker style="width: 100%" @change="onChangeStartTime" placeholder="开始时间"/>
          </a-col>
          <a-col :span="5">
            <a-date-picker style="width: 100%" @change="onChangeEndTime" placeholder="结束时间"/>
          </a-col>
          <a-col :span="5">
            <a-input style="width: 100%" v-model="corpNameInput" placeholder="公司名称"/>
          </a-col>
          <a-col :span="9">
            <div style="width: 100%">
              <a-button type="primary" icon="search" @click="serach">查询</a-button>
              <a-button type="primary" @click="reset">重置</a-button>
              <a-button icon="download" @click="outCsv">导出CSV</a-button>
            </div>
          </a-col>
        </a-row>
      </div>

        <!-- 表格 -->
        <a-table
        :columns="columns"
        :pagination="false"
        :dataSource="data">
            <template slot="gmtCreate" slot-scope="gmtCreate">
              {{ new Date(gmtCreate).toLocaleString() }}
            </template>
            <template slot="creatorIsAdmin" slot-scope="creatorIsAdmin">
              {{ creatorIsAdmin ? '是' : '否' }}
            </template>
            <template slot="creatorIsBoss" slot-scope="creatorIsBoss">
              {{ creatorIsBoss ? '是' : '否' }}
            </template>
            <template slot="columnAction" slot-scope="text, record">
              <a-button type="primary" @click="makeAcall(record.corpId)">打电话</a-button>
            </template>
        </a-table>
        <br/>
        <a-pagination showSizeChanger
                      :current="pageCurrent"
                      :pageSize.sync="pageSize"
                      :total="pageTotal"
                      @change="changeThePage"
                      @showSizeChange="onShowSizeChange"/>
      </div>
  </div>
</template>

<script>
import { customerListFetch, exportCsv, customerMakeACall } from '@/services/custormer'

export default {
  data() {
    return {
      startDateInput: '',
      endDateInput: '',
      corpNameInput: '',
      columns: [
        {
          title: '公司名称',
          dataIndex: 'corpName'
        }, {
          title: '创建时间',
          dataIndex: 'gmtCreate',
          scopedSlots: { customRender: 'gmtCreate' },
        }, {
          title: '所在行业',
          dataIndex: 'industry'
        }, {
          title: '公司人数',
          dataIndex: 'corpCount'
        }, {
          title: '联系人userId',
          dataIndex: 'creatorUserId'
        }, {
          title: '创建者名称',
          dataIndex: 'creatorName'
        }, {
          title: '创建者是管理员',
          dataIndex: 'creatorIsAdmin',
          scopedSlots: { customRender: 'creatorIsAdmin' },
        }, {
          title: '创建者是老板',
          dataIndex: 'creatorIsBoss',
          scopedSlots: { customRender: 'creatorIsBoss' },
        }, {
          title: '操作',
          dataIndex: 'columnAction',
          scopedSlots: { customRender: 'columnAction' },
        }
      ],
      data: [],
      pageCurrent: 1,
      pageSize: 10,
      pageTotal: 0
    }
  },
  created() {
    this.getList();
  },
  mounted() {},
  methods: {
    // 获取列表
    getList() { 
      this.loading = true;

      // 请求参数
      let startDate = this.startDateInput;
      let endDate = this.endDateInput;
      let corpName = this.corpNameInput;

      let pageNumber = this.pageCurrent;
      let pageSize = this.pageSize;
 
      customerListFetch(startDate,endDate,corpName,pageNumber,pageSize).then((res) => {
        if (res.errcode === '0') {
          this.loading = false;
          // 分页数据
          this.pageCurrent = res.pageNumber;
          this.pageSize = res.pageSize;
          this.pageTotal = res.total;
          // 表单数据
          this.data = res.data;
        }
      });
    },
    serach() {
      this.pageCurrent = 1;
      this.getList();
    },
    reset() {
      this.startDateInput = '';
      this.endDateInput = '';
      this.corpNameInput = '';
      // 重置之后，查询一次
      this.getList();
    },
    onShowSizeChange(current, pageSize) {
      this.pageCurrent = 1;
      this.getList();
    },
    changeThePage(page, pageSize) {
      this.pageCurrent = page;
      this.pageSize = pageSize;
      this.getList();
    },
    onChangeStartTime(date, dateString) {
      // 毫秒值
      this.startDateInput = date._d.getTime();
    },
    onChangeEndTime(date, dateString) {
      // 毫秒值
      this.endDateInput = date._d.getTime();
    },
    outCsv() {
      if (this.startDateInput == '' || this.endDateInput == '') {
        this.$message.warning('请选择起止日期！');
        return;
      }
      if (this.startDateInput > this.endDateInput) {
        this.$message.warning('开始时间不能大于结束时间！');
        return;
      }

      if (this.endDateInput - this.startDateInput > 30 * 1000 * 60 * 60 * 24) {
        this.$message.warning('导出CSV不能超过30天！');
        return;
      }

      // 请求导出接口
      let startDate = this.startDateInput;
      let endDate = this.endDateInput;
      // let startDate = '2018-12-25';
      // let endDate = '2018-12-29';
      let token = localStorage.getItem('login_token');
      window.open(`${R_URL.BASE_URL}${R_URL.customer.EXPORT_CSV}?startDate=${startDate}&endDate=${endDate}&token=${token}`);
    },
    makeAcall (corpId) {
      customerMakeACall(corpId).then((res) => {
        let resText = JSON.stringify(res);
        this.$message.info(resText);
      });
    }
  },
  computed: {
    rowSelection() {
      return {}
    }
  },
}
</script>
<style lang='scss' scoped>
#cl {
  height: 100%;
  padding: 24px;
  padding-top: 88px;
  background-color: #f0f2f5;
  .c-main {
    background-color: #fff;
    padding: 23px 32px 17px;
    .m-header {
      overflow: hidden;
      margin-bottom: 24px;
    }
  }
}
button {
  margin-left: 5px;
}
</style>
