import request from '@/utils/request'


export function customerListFetch(startDate, endDate, corpName, pageNumber, pageSize) { // 客户管理列表
  return request.get(`${R_URL.customer.CUSTOMER_LIST}?startDate=${startDate}&endDate=${endDate}&corpName=${corpName}&pageNumber=${pageNumber}&pageSize=${pageSize}`, {
  })
    .then(res => res.data)
}

// 客户管理 打电话按钮
export function customerMakeACall(corpId) { 
  return request.post(`${R_URL.customer.CALL_CORP_ADMIN}?corpId=${corpId}`, {
  })
    .then(res => res.data)
}

// 暂未调用
export function exportCsv(startDate, endDate) {
  var params = new URLSearchParams();
  params.append('startDate', startDate);
  params.append('endDate', endDate);

  // `${R_URL.customer.EXPORT_CSV}?startDate=${startDate}&endDate=${endDate}`
  return request.get(`${R_URL.customer.EXPORT_CSV}`, {
    params,
    headers: {
      // 'Content-Type': 'application/x-www-form-urlencoded',
      // 'Content-Type': 'application/csv',
      'Accept': 'multipart/form-data',
    }
  })
    .then(res => res.data)
}



